@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Serif:wght@700&family=Roboto:ital,wght@0,400;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@700;900&display=swap);

* {
  margin: 0;
  padding: 0;
}
html {
  scroll-behavior: smooth;
}
body {
  background-color: rgb(255, 255, 255);
}
@media (max-width: 1200px) {
  .nav-disapear {
    display: none;
  }
}
@media (min-width: 1200px) {
  .bm-burger-button {
    display: none;
  }
}

/* Position and sizing of burger button */
.bm-burger-button {
  position: absolute;
  width: 36px;
  height: 30px;
  right: 36px;
  top: 36px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #373a47;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #ed4385;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  -webkit-transform: scale(2);
          transform: scale(2);
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #ffffff;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%;
}

/* General sidebar styles */
.bm-menu {
  background: #ed4385;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #ed4385;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em;
}

/* Individual item */
.bm-item {
  font-size: 20px;
  padding-bottom: 20px;
  text-decoration: none;
  color: white;
  display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

.container {
  padding-top: 200px;
  background-color: #f1f4f6;
  position: relative;
}
.content {
  padding-bottom: 250px;
  width: 70vw;
  margin: auto;
  background-color: #f1f4f6;
}
.content .text {
  padding: 100px 50px 50px 50px;
  width: 80%;
  vertical-align: center;
}
.content .text h1 {
  font-weight: 600;
  padding: 0px 0px 50px 0px;
  font-size: 42px;
}
.content .text p {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  line-height: 30px;
  font-size: 21px;
  padding: 0px 0px 50px 0px;
}
.content .text a {
  font-family: "Roboto", sans-serif;
  color: #5722cd;
  text-decoration: none;
  font-weight: 500;
  font-size: 14px;
  letter-spacing: 2px;
}
.content .image img {
  box-shadow: -1px 13px 15px -1px rgba(0, 0, 0, 0.24);
  width: 100%;
  max-height: 500px;
  min-height: 400px;
}
.content .image img:hover {
  cursor: pointer;
}
.content .card-left {
  z-index: 10;
  position: relative;
  padding: 100px 0px 0px 0px;
  display: grid;
  grid-template-columns: 0.8fr 1.2fr;
  grid-template-rows: 1fr;
  grid-gap: 0px 0px;
  gap: 0px 0px;
}
.content .card-right {
  z-index: 10;
  position: relative;
  padding: 100px 0px 0px 0px;
  display: grid;
  grid-template-columns: 1.2fr 0.8fr;
  grid-template-rows: 1fr;
  grid-gap: 0px 0px;
  gap: 0px 0px;
}
.background-text {
  font-weight: bold;
  font-family: "Roboto", sans-serif;
  font-size: 600px;
  color: #ffffff;
  position: absolute;
  top: 5%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  z-index: 1;
}
.text .link {
  display: flex;
  align-items: center;
}
.text .link::after {
  content: "";
  width: 15px;
  height: 1px;
  display: inline-block;
  background: #5722cd;
  margin-left: 15px;
  transition: width 0.2s ease-in;
}
.text .link:hover::after {
  width: 60px;
}
.triangle-content {
  z-index: 1;
  position: absolute;
  bottom: 0;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 100px 0 0 99vw;
  border-color: transparent transparent transparent white;
}

@media (max-width: 1200px) {
  .background-text {
    font-size: 300px;
  }
  .content .text {
    padding: 50px 50px 50px 50px;
  }
  .content {
    width: 90%;
  }
  .content .image img {
    min-height: 350px;
  }
}
@media (max-width: 768px) {
  .background-text {
    font-size: 150px;
  }
  .card-left .text {
    grid-column-start: 1;
    grid-column-end: five;
    grid-row-start: row1-start;
    grid-row-end: 0;
  }
  .card-right .text {
    grid-column-start: 1;
    grid-column-end: five;
    grid-row-start: row1-start;
    grid-row-end: 0;
  }
  .content .card-left {
    grid-template-columns: 100%;
    grid-template-rows: repeat(1fr, auto);
  }
  .content .card-right {
    grid-template-columns: 100%;
    grid-template-rows: repeat(1fr, auto);
  }
}

.footer {
  background-color: black;
  height: 80px;
}
.footer p {
  padding: 30px 0px 0px 0px;
  color: #b5b5b5;
  text-align: center;
  font-family: "Roboto", sans-serif;
}

.form-neutral {
  background: rgb(87, 34, 205);
  background: linear-gradient(
    180deg,
    rgba(87, 34, 205, 1) 50%,
    rgba(64, 84, 178, 1) 100%
  );
}
.form-wrap {
  padding-top: 50px;
  max-width: 770px;
  margin: auto;
}
.form h1 {
  padding-bottom: 20px;
  color: white;
  font-size: 54px;
  text-align: center;
}
.form p {
  font-size: 22px;
  color: white;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  text-align: center;
  padding-bottom: 25px;
}
.form form label {
  font-family: "Roboto", sans-serif;
  color: white;
  font-size: 17px;
}
.form form input {
  margin-bottom: 30px;
  padding: 5px;
  height: 25px;
  width: 100%;
}
.form form textarea {
  font-family: "Roboto", sans-serif;
  resize: none;
  padding: 5px;
  width: 100%;
}
.form form .button {
  padding-top: 100px;
  padding-bottom: 100px;
  text-align: center;
}
.form form .button button {
  cursor: pointer;
  transition-duration: 0.3s;
  box-shadow: -1px 13px 15px -1px rgba(0, 0, 0, 0.24);
  border-radius: 30px;
  padding: 12px 90px 12px 90px;
  font-family: "Roboto", sans-serif;
  letter-spacing: 2px;
  font-weight: 700;
  font-size: 18px;
  color: white;
  background-color: #ed4385;
  border: none;
}
/*Hovers*/
.form form .button button:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
/*Responsivnes*/
@media (max-width: 764px) {
  .form-wrap {
    width: 90%;
  }
}

header {
  position: relative;
  text-align: center;
  background-image: url("https://www.rekupe.com/wp-content/uploads/2019/01/HP_Background.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 75vh;
}
header h1 {
  color: white;
  font-size: 58px;
  font-family: "PT Serif", serif;
  padding: 170px 0px 0px 0px;
  line-height: 1;
  letter-spacing: -2px;
}

header p {
  margin: 34px 0px 0px 0px;
  font-size: 30px;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  color: white;
}
header button {
  z-index: 5;
  color: white;
  background-color: #ed4385;
  font-family: "Roboto", sans-serif;
  border: none;
  border-radius: 40px;
  font-size: 18px;
  margin: 35px 0px 0px 0px;
  padding: 23px 40px 23px 40px;
  box-shadow: 0px 13px 22px -8px rgba(0, 0, 0, 0.3);
  letter-spacing: 1px;
  transition: 0.2s;
}
header button:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  transition: 0.2s;
  cursor: pointer;
}
header .Triangle {
  z-index: 1;
  position: absolute;
  bottom: 0;
  border-style: solid;
  border-width: 100px 0 0 99vw;
  border-color: transparent transparent transparent #ffffff;
}
/* Animations */
header h1,
header p,
header button {
  position: relative;
  visibility: hidden;
  -webkit-animation-name: moveUp;
          animation-name: moveUp;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@-webkit-keyframes moveUp {
  0% {
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    opacity: 0;
    margin-bottom: 40px;
    top: 200px;
    visibility: hidden;
  }
  100% {
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    opacity: 1;
    margin-bottom: 0px;
    top: 0px;
    visibility: visible;
  }
}
@keyframes moveUp {
  0% {
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    opacity: 0;
    margin-bottom: 40px;
    top: 200px;
    visibility: hidden;
  }
  100% {
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    opacity: 1;
    margin-bottom: 0px;
    top: 0px;
    visibility: visible;
  }
}
/*Responsivnes*/
@media (max-width: 1200px) {
}
@media (max-width: 764px) {
  header h1 {
    font-size: 30px;
  }
  header p {
    font-size: 20px;
  }
  header button {
    font-size: 16px;
  }
}

.Main {
  background-color: white;
  height: 80vh;
  z-index: 2;
  position: relative;
}
.Main .text {
  margin-top: 260px;
  text-align: center;
  font-family: "PT Serif", serif;
  font-size: 30px;
  color: #5722cd;
}
.Main p {
  margin-bottom: 40px;
}
.Main .underText {
  color: black;
  font-family: "Roboto", sans-serif;
  font-size: 21px;
  margin-top: 100px;
}
.Main .underText a {
  color: #5722cd;
  text-decoration: none;
}
.backgroundText {
  font-weight: bold;
  font-family: "Roboto", sans-serif;
  color: #5822cd13;
  position: absolute;
  font-size: 600px;
  top: 20%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  z-index: -1;
  overflow: hidden;
}
.Triangle {
  z-index: -1;
  position: absolute;
  bottom: 0;
  border-style: solid;
  border-width: 100px 0 0 99vw;
  border-color: transparent transparent transparent #f1f4f6;
}

/* Animations */
.Main .text {
  visibility: hidden;
  -webkit-animation-name: easeIn;
          animation-name: easeIn;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@-webkit-keyframes easeIn {
  0% {
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    opacity: 0;
    visibility: hidden;
  }
  100% {
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    opacity: 1;
    visibility: visible;
  }
}
@keyframes easeIn {
  0% {
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    opacity: 0;
    visibility: hidden;
  }
  100% {
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    opacity: 1;
    visibility: visible;
  }
}
/*Responsivnes*/
@media (max-width: 1200px) {
  .backgroundText {
    font-size: 300px;
  }
}
@media (max-width: 768px) {
  .backgroundText {
    font-size: 150px;
  }
  .Main .text {
    font-size: 25px;
  }
}

nav {
  z-index: 2;
  position: relative;
  font-family: "Roboto", sans-serif;
}
nav ul {
  list-style-type: none;
  float: right;
  margin: 25px 265px 0px 20px;
}
nav ul li {
  display: inline;
  padding: 0px 20px 0px 20px;
  border-bottom: 4px solid transparent;
  transition: 0.1s;
  padding-bottom: 15px;
}
nav ul li a {
  color: white;
  text-decoration: none;
  font-size: 15px;
}
nav ul li:hover {
  border-bottom: 5px solid #ed4385;
}

.neutral {
